.general {
  display: flex;
  flex-direction: column;
  gap: 50px;

  &__information {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  &__title {
    font-family: Roboto Condensed;
    font-size: 25px;
  }

  &__text {
    font-family: Roboto Condensed;
    font-size: 20px;
  }

  &__video-list {
    display: flex;
    justify-content: space-evenly;
  }

  &__left-video-block {
    display: flex;
    flex-direction: column;

    justify-content: space-between;
  }

  &__video-item {
    display: flex;
  }

  &__video {
    width: 500px;
  }

  &__video--vertical {
    width: 350px;
    height: 100%;
  }

  &__btn-link {
    margin: 0 auto;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 300px;
    height: 56px;

    border-radius: 10px;
    font-weight: 700;
    font-size: 20px;

    background-color: #ff6400;
    color: #fff;

    transition: all 0.3s linear;
    cursor: pointer;

    &:hover {
      background-color: #1b2129;
    }
  }
}
